export const state = () => ({
  token: '',
  products: '',
  skus: [],
  price: [],
  alert: false,
});

export const getters = {
  getToken(state) {
    return state.token;
  },
  getProducts(state) {
    return state.products;
  },
  getSkus(state) {
    return state.skus;
  },
  getPrice(state) {
    return state.price;
  },
  getAlert(state) {
    return state.alert;
  },
};

export const actions = {
  setToken({ commit }, newToken) {
    commit('newToken', newToken);
  },
  setProducts({ commit }, products) {
    commit('addProducts', products);
  },
  setSkus({ commit }, newSkus) {
    commit('addSkus', newSkus);
  },
  setPrice({ commit }, newPrice) {
    commit('addPrice', newPrice);
  },
  setAlert({ commit }, newState) {
    commit('addAlertState', newState);
  },
};

export const mutations = {
  newToken(state, newToken) {
    state.token = newToken;
  },
  addProducts(state, products) {
    state.products = products;
  },
  addSkus(state, newSkus) {
    state.skus = newSkus;
  },
  addPrice(state, newPrice) {
    state.price = newPrice;
  },
  addAlertState(state, newState) {
    state.alert = newState;
  },
};
