import {
  SfHeader,
  SfImage,
  SfButton,
  SfBadge,
  SfSearchBar,
  SfIcon,
  SfOverlay,
} from '@storefront-ui/vue';
import SearchResults from './SearchResults.vue';
import debounce from 'lodash/debounce';
import useUiState from '~/composables/useUiState';
import { onSSR } from '@vue-storefront/core';
import { computed, ref, useRouter } from '@nuxtjs/composition-api';

import {
  useCart,
  useWishlist,
  useUser,
  cartGetters,
  searchGetters,
  useCategory,
  useSearch,
} from '@vue-storefront/shopify';
import useUiHelpers from '~/composables/useUiHelpers';
import LocaleSelector from './LocaleSelector';

export default {
  components: {
    SearchResults,
    SfHeader,
    SfImage,
    SfIcon,
    LocaleSelector,
    SfButton,
    SfOverlay,
    SfBadge,
    SfSearchBar,
  },
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  setup() {
    const { toggleCartSidebar, toggleWishlistSidebar, toggleLoginModal } =
      useUiState();
    const { changeSearchTerm, getFacetsFromURL } = useUiHelpers();
    const { isAuthenticated, load: loadUser } = useUser();
    const { cart, load: loadCart } = useCart();
    const { search: headerSearch, result } = useSearch('header-search');
    const { search } = useCategory('menuCategories');
    const { load: loadWishlist } = useWishlist();
    const router = useRouter();

    const categories = [
      { id: 'clothes1', handle: 'clothes', title: 'Clothes' },
      { id: 'accessories1', handle: 'accessories', title: 'Accessories' },
      { id: 'shoes1', handle: 'shoes', title: 'Shoes' },
    ];

    const curCatSlug = ref(getFacetsFromURL().categorySlug);
    const cartTotalItems = computed(() => {
      const count = cartGetters.getTotalItems(cart.value);
      return count ? count?.toString() : null;
    });

    const accountIcon = computed(() =>
      isAuthenticated.value ? 'profile_fill' : 'profile',
    );

    // TODO: https://github.com/DivanteLtd/vue-storefront/issues/4927
    const handleAccountClick = () => {
      if (isAuthenticated.value) {
        return router.push('/my-account');
      }

      toggleLoginModal();
    };

    // #region Search Section
    const isSearchOpen = ref(false);
    const searchResults = ref(null);
    const term = ref(getFacetsFromURL().term);
    const handleSearch = debounce(async (searchTerm) => {
      if (!searchTerm.target) {
        term.value = searchTerm;
      } else {
        term.value = searchTerm.target.value;
      }

      await headerSearch({
        term: term.value,
      });
    }, 1000);
    const closeSearch = () => {
      if (!isSearchOpen.value) return;
      term.value = '';
      isSearchOpen.value = false;
    };

    searchResults.value = {
      products: computed(() => searchGetters.getItems(result.value)),
    };
    // #endregion Search Section

    onSSR(async () => {
      await loadUser();
      await loadCart();
      await loadWishlist();
      await search({ slug: '' });
    });

    return {
      accountIcon,
      cartTotalItems,
      closeSearch,
      handleAccountClick,
      toggleCartSidebar,
      toggleWishlistSidebar,
      changeSearchTerm,
      term,
      handleSearch,
      curCatSlug,
      searchResults,
      categories,
      isSearchOpen,
    };
  },
};
