<template>
  <div id="cart">
    <SfSidebar
      v-e2e="'sidebar-cart'"
      :visible="isCartSidebarOpen"
      title="My Cart"
      class="sf-sidebar--right"
      @close="toggleCartSidebar"
    >
      <template #content-top>
        <SfProperty
          v-if="totalItems"
          class="sf-property--large cart-summary desktop-only"
          name="Total items"
          :value="totalItems"
        />
      </template>
      <transition name="sf-fade" mode="out-in">
        <div v-if="totalItems" key="my-cart" class="my-cart">
          <div class="collected-product-list">
            <transition-group name="sf-fade" tag="div">
              <SfCollectedProduct
                v-for="product in products"
                :key="product.sku"
                v-e2e="'collected-product'"
                :image="product.coverImage"
                :title="product.name"
                :regular-price="$n(product.price, 'currency')"
                :special-price="
                  cartGetters.getItemPrice(product).special &&
                  $n(cartGetters.getItemPrice(product).special, 'currency')
                "
                :stock="99999"
                class="collected-product"
                @click:remove="removeItem({ product })"
              >
                <!--<template #configuration>
                  <div class="collected-product__properties">
                    <SfProperty
                      v-for="(attribute, key) in cartGetters.getItemAttributes(
                        product,
                        ['color', 'size'],
                      )"
                      :key="key"
                      :name="key"
                      :value="attribute"
                    />
                  </div>
                </template>-->
                <template #input>
                  <div class="sf-collected-product__quantity-wrapper">
                    <SfQuantitySelector
                      :disabled="loading"
                      :qty="cartGetters.getItemQty(product)"
                      class="sf-collected-product__quantity-selector"
                      @input="
                        updateQuantity({ product, quantity: Number($event) })
                      "
                    />
                  </div>
                </template>
              </SfCollectedProduct>
            </transition-group>
          </div>
        </div>
        <div v-else key="empty-cart" class="empty-cart">
          <div class="empty-cart__banner">
            <SfImage
              alt="Empty bag"
              class="empty-cart__image"
              src="/icons/empty-cart.svg"
            />
            <SfHeading
              title="Your cart is empty"
              :level="2"
              class="empty-cart__heading"
              description="Looks like you haven’t added any items to the bag yet. Start
              shopping to fill it in."
            />
          </div>
        </div>
      </transition>
      <template #content-bottom>
        <transition name="sf-fade">
          <div v-if="totalItems">
            <SfProperty
              name="Estimated Total"
              class="sf-property--full-width sf-property--large my-cart__total-price"
            >
              <template #value>
                <SfPrice :regular="$n(totals.subtotal, 'currency')" />
              </template>
            </SfProperty>
            <SfLink
              link="javascript:void(0);"
              @click="handleCheckout(checkoutURL, parseFloat(totals.subtotal))"
            >
              <SfButton
                class="sf-button--full-width color-secondary sf-proceed_to_checkout"
                @click="toggleCartSidebar"
              >
                {{ $t('Go to checkout') }}
              </SfButton>
            </SfLink>
          </div>
          <div v-else>
            <SfButton
              class="sf-button--full-width color-primary"
              @click="toggleCartSidebar"
              >{{ $t('Go back shopping') }}</SfButton
            >
          </div>
        </transition>
      </template>
    </SfSidebar>
  </div>
</template>
<script>
import {
  SfSidebar,
  SfHeading,
  SfButton,
  SfIcon,
  SfProperty,
  SfPrice,
  SfCollectedProduct,
  SfImage,
  SfLink,
  SfQuantitySelector,
} from '@storefront-ui/vue';
import { computed, onBeforeMount } from '@nuxtjs/composition-api';
import { onSSR } from '@vue-storefront/core';
import { useCart, useUser, cartGetters } from '@vue-storefront/shopify';
import { useUiState, useUiNotification } from '~/composables';
import debounce from 'lodash.debounce';

export default {
  name: 'Cart',
  components: {
    SfSidebar,
    SfButton,
    SfHeading,
    SfIcon,
    SfLink,
    SfProperty,
    SfPrice,
    SfCollectedProduct,
    SfImage,
    SfQuantitySelector,
  },
  setup(_, { root }) {
    const { isCartSidebarOpen, toggleCartSidebar } = useUiState();
    const {
      cart,
      removeItem,
      updateItemQty,
      load: loadCart,
      loading,
    } = useCart();
    const { isAuthenticated } = useUser();
    const { send: sendNotification, notifications } = useUiNotification();
    const products = [
      {
        sku: 'TEST123',
        id: 'test1',
        name: 'Test',
        coverImage:
          'https://cdn.shopify.com/s/files/1/0407/1902/4288/products/ZA030136_011_p_600x600.jpg?v=1593091618',
        price: 300,
        type: 'Blouse',
        rating: 4,
        description: 'This is a test',
      },
    ];
    const totals = { subtotal: 269.0 };
    const totalItems = 1;
    const checkoutURL =
      'https://vsf-next-pwa.myshopify.com/40719024288/checkouts/3bb5e8d94d75ead8c44fab6a3ac02a81';

    const handleCheckout = (checkoutUrl) => {
      setTimeout(() => {
        window.location.href = checkoutUrl;
      }, 400);
    };

    onSSR(async () => {
      await loadCart();
    });

    onBeforeMount(async () => {
      await loadCart().then(() => {
        if (cart && cart?.value?.orderStatusUrl !== null) {
          root.$cookies.remove(`${root.$config.appKey}_cart_id`);
        }
      });
    });

    const updateQuantity = debounce(async ({ product, quantity }) => {
      await updateItemQty({ product, quantity });
    }, 500);

    return {
      updateQuantity,
      loading,
      isAuthenticated,
      products,
      removeItem,
      handleCheckout,
      checkoutURL,
      isCartSidebarOpen,
      toggleCartSidebar,
      totals,
      totalItems,
      cartGetters,
      sendNotification,
      notifications,
    };
  },
};
</script>

