import axios from 'axios';

const getToken = (updateToken, username, password) => {
  let result;
  axios({
    method: 'GET',
    url: '/getToken.js',
    headers: {
      username,
      password,
    },
  })
    .then((res) => {
      console.log(res);
      updateToken(JSON.parse(JSON.stringify(res.data.access_token)));
    })
    .catch((err) => console.log(err));

  return result;
};

export default getToken;
