import { SfModal } from '@storefront-ui/vue';
import { ref } from '@nuxtjs/composition-api';
import { useUiState } from '~/composables';
import getToken from '~/api/getToken';

export default {
  name: 'LoginModal',
  components: {
    SfModal,
  },

  data() {
    return {
      username: '',
      password: '',
      messageSingIn: 'Sing In',
      messageLogOut: 'Yes, close session',
      disabledButton: false,
    };
  },

  setup(_, { root }) {
    const { isLoginModalOpen, toggleLoginModal } = useUiState();

    const closeModal = () => {
      toggleLoginModal();
    };

    const isLogin = ref(root.$cookies.get('_token') ? true : false);

    return {
      isLoginModalOpen,
      toggleLoginModal,
      closeModal,
      isLogin,
    };
  },

  computed: {
    tokenAuth() {
      return this.$store.getters['getToken'];
    },
  },

  methods: {
    setTokenAuth(newToken) {
      this.$cookies.set('_token', newToken, {
        path: '/',
        maxAge: 31536000,
      });
      this.$store.dispatch('setToken', newToken);
    },

    singIn(e) {
      getToken(this.setTokenAuth, this.username, this.password);
      if (!this.isLogin) {
        this.$router.go();
      }
      if (e.isTrusted) {
        this.messageSingIn = 'loading...';
        this.disabledButton = true;
      }
    },

    singOut(e) {
      this.$cookies.remove('_token');
      if (this.isLogin) {
        this.$router.push('/');
        this.$router.go();
      }
      if (e.isTrusted) {
        this.messageLogOut = 'loading...';
        this.disabledButton = true;
      }
    },
  },
};
